@media screen and (max-width: 810px) {
    #basket_form {
        width: 80%;
        text-align: center;
        padding: 19px 38px 0 38px;
        .row-flex {
            display: block;
        }
        .close-wp {
            display: block;
            margin-right: 0px;
            text-align: right;
        }
        .close-btn {
            display: inline-block;
            width: 30px;
            height: 30px;
            padding: 0;
            padding-left: 0;
            margin: 0;
            i {
                font-size: 20px;
            }
        }
        .btn-wp {
            display: inline-block;
            width: auto;
            margin: 0 0 30px 0;
        }
        .tov-photo-wp, .tov-name-wp, .tov-price-wp,.tov-count-wp {
            display: block;
            margin-right: 0;
            height: auto;
            margin: 0 auto 10px auto;
        }
        .tov-count-wp {
            position: relative;
            &:after {
                content: 'шт.';
                font-size: 16px;
                color: black;
                right: -25px;
                top: 9px;
                position: absolute;
                text-shadow: 1px 1px 2px #dbdbdb;
            }
        }
        .tov-count {
            width: 100%;
            border: 1px solid #ddd8e6;
            border-bottom-color: #ddd8e6;
            border-radius: 0px;
            box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1);
            height: 40px;
            font-size: 16px;
            text-align: center;
        }
        .tov-name-wp {
            width: 100%;
            height: auto;
            display: block;
            text-align: center;
            margin: 20px 0 20px 0;
        }
        .tov-price {
            font-size: 20px;
        }
        .tov-old-price {
            font-size: 16px;
        }
        .order-name {
            display: inline-block;
            font-size: 18px;
        }
        .btn-wp {
            display: block;
        }
        .default-btn {
            background-color: #673ab7;
            color: white;
            display: block;
            margin: 0 auto 10px auto;
            &:first-child {
                margin: 0 auto 10px auto;
            }
        }
        .close2 {
            background-color: white;
            color: #673ab7;
            border: 1px solid #673ab7;
            &:hover {
                color: #673ab7 !important;
                border: 1px solid #ffc107;
                background-color: #ffc107;
            }
            &:focus {
                color: #ffc107 !important;
                border: 1px solid #ffc107;
                background-color: #ffc107;
            }
        }
        .total-price-wp {
            display: inline-block;
            width: auto;
            margin: 10px 0 0 0;
            .all-red-price {
                display: block;
                margin-bottom: 5px;
                margin-top: 10px;
            }
            .tov-old-price {
                margin-left: 0;
                margin-bottom: 20px;
                display: block;
            }
        }
        > div {
            display: block;
        }
        .order-list {
            .order-inside-itm {
                .tov-all-price-wp, .tov-all-price-wp {
                    width: 117px;
                }
            }
        }
    }
}
